.landing .landing-header h1 {
    margin-bottom: 2rem;
}
.landing .landing-header .paragraph {
    font-size: 1.3rem;
}
.landing .landing-header img {
    width: 60vw;
    max-width: 400px;
    margin: 0 auto;
}
.landing .landing-content {
    background: var(--background);
    padding-top: 3rem;
}
.landing .landing-content h2 {
    color: var(--title);
}
.landing .landing-content p {
    color: var(--text);
}
.landing .landing-content .landing-content-item {
    padding-bottom: 2rem;
    max-width: 720px;
    margin: auto;
    
}
.landing .landing-content .landing-content-item .text,
.landing .landing-content .landing-content-item .image {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}
.landing .landing-content .landing-content-item svg {
    padding-top: 1rem;
    padding-bottom: 1rem;
    width: 100%;
    max-width: 370px;
}
@media screen and (min-width: 576px) {
    .landing .landing-content .landing-content-item svg {
        padding-top: 0;
        padding-bottom: 0;
    }
}
.landing .landing-content .landing-content-item a {
    color: var(--title);
    text-decoration: none;
    background: var(--background-two-ts);
    padding: 1rem;
    transition: all 0.3s ease-in-out;
    display: block;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 0 auto;
}
.landing .landing-content .landing-content-item a:hover {
    background: var(--background-two);
}
.landing .landing-content .landing-content-item+ .landing-content-item {
    padding-top: 2rem;
    border-top: var(--title) 1px solid;
}
.landing .landing-content .landing-content-item .text h2 {
    margin-bottom: 1.5rem;
}


.loading {
  height:4px;
  background:
   linear-gradient(var(--primary) 0 0),
   linear-gradient(var(--primary) 0 0),
   #d7b8fc;
 background-size: 60% 100%;
 background-repeat: no-repeat;
 -webkit-animation: p6 3s infinite;
         animation: p6 3s infinite;
 width: 100%;
}
.rescurepass-container .loading, 
.register-container .loading, 
.product-buy .loading, 
.login-container .loading {
  width: 150px;
  margin: 0 auto;
}

@-webkit-keyframes p6 {
 0%   {background-position:-150% 0,-150% 0}
 66%  {background-position: 250% 0,-150% 0}
 100% {background-position: 250% 0, 250% 0}
}

@keyframes p6 {
 0%   {background-position:-150% 0,-150% 0}
 66%  {background-position: 250% 0,-150% 0}
 100% {background-position: 250% 0, 250% 0}
}
